import axios from "axios";

const HOME_BASE_URL = 'http://dighighs.com/rioaarman';

class HomeService{


    async productList(){
        let res = await axios.post(HOME_BASE_URL + '/api/product')
        return res
    }

    async productDetail(data){
        let res = await axios.post(HOME_BASE_URL + '/api/product-detail',data)
        return res
    }

    async homeScreen(){
        let res = await axios.post(HOME_BASE_URL + '/api/home-screen')
        return res
    }

}

export default new HomeService();