import axios from 'axios';
import React, { Component, useState } from 'react';
import { Link } from 'react-router-dom';
const HOME_BASE_URL = 'http://dighighs.com/rioaarman';

function SignIn() {
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    let submitHandler = async (e) => {
        e.preventDefault()
        const header = {
            headers: { 'Content-Type': 'application/json' },
            withCredentials: true,
            mode:'cors'
        }
        let data = {
            email,
            password
        }
        let res = await axios.post(HOME_BASE_URL + '/api/login',data,header)
        if(res.status == 200){
            console.log(res.data)
        }
    }
        return (
            <div>
                <div className="section breedcrumb">
                    <div className="breedcrumb__img-wrapper">
                        <img src="assets/images/banner/breedcrumb.jpg" alt="breedcrumb" />
                        <div className="container">
                        <ul className="breedcrumb__content">
                            <li>
                            <Link to="/">
                                <i className="fa fa-home iconss"></i>
                                <span> {'>'} </span>
                            </Link>
                            </li>
                            <li className="active"><Link to="faq.html">Sign in</Link></li>
                        </ul>
                        </div>
                    </div>
                </div>
            
                <section className="sign-in section section--xl">
                <div className="container">
                    <div className="form-wrapper">
                    <h6 className="font-title--sm">Sign in</h6>
                    <form onSubmit={(e)=>submitHandler(e)}>
                        <div className="form-input">
                        <input value={email} onChange={(e)=>setEmail(e.target.value)} type="email" placeholder="Email" />
                        
                        </div>
                        <div className="form-input">
                        <input value={password} onChange={(e)=>setPassword(e.target.value)} type="password" placeholder="Password" id="password" />
                        <button
                            type="button"
                            className="icon icon-eye">
                        <i className="fa fa-eye"></i>
                        </button>
                        
                        </div>
                        <div className="form-wrapper__content">
                        <div className="form-check">
                            <input
                            className="form-check-input"
                            type="checkbox"
                            value=""
                            id="remember"
                            />
                            <label className="form-check-label" htmlFor="remember">
                            Remember Me
                            </label>
                        </div>
                        <Link to="#">Forget Password </Link>
                        </div>
                        <div className="form-button">
                        <button className="button button--md w-100">Login</button>
                        </div>
                        <div className="form-register">
                        Don't have account ? <Link to="/register">Register</Link>
                        </div>
                    </form>
                    </div>
                </div>
                </section>
            </div>
        );
}

export default SignIn;