import axios from "axios";

const HOME_BASE_URL = 'http://dighighs.com/rioaarman';
const header = {
    headers: { 'Content-Type': 'application/json' },
    withCredentials: true,
    mode:'cors'
}
class AuthService{

    login(data){
        let data1 = JSON.stringify(data)
        return axios.post(HOME_BASE_URL+'/api/login',data1,header);
    }

    async register(data,header){
        return await axios.post(HOME_BASE_URL+'/api/register',data,header);
    }

}

export default new AuthService();